var containers = ''
var canvas = {}
var centerX = 0
var centerY = 0
var color = ''
var context = {}
var element = {}
var radius = 0

var requestAnimFrame = (function () {
  return (
    window.requestAnimationFrame ||
      window.mozRequestAnimationFrame ||
      window.oRequestAnimationFrame ||
      window.msRequestAnimationFrame ||
      function (callBack) {
        window.setTimeout(callBack, 10)
      }
  )
}(this))

var draw = function () {
  context.beginPath()
  context.arc(centerX, centerY, radius, 0, 2 * Math.PI, false)
  context.fillStyle = color
  context.fill()
  radius += 10
  if (radius < element.offsetWidth) {
    requestAnimFrame(draw)
  }
}

var getElementPosition = function (e) {
  var x = 0; var y = 0
  while (e != null) {
    x += e.offsetLeft
    y += e.offsetTop
    e = e.offsetParent
  }
  return { x: x, y: y }
}

var press = function () {
  // 如果这个元素的位置内只有一个手指的话
  if (event.targetTouches.length === 1) {
    // console.log('当前只有一个触点')
  }
  // event.toElement.parentElement.dataset.color
  color = 'rgba(0, 0, 0, 0.08)'
  element = event.srcElement
  context = element.getContext('2d')
  radius = 0
  centerX = Math.abs(event.touches[0].pageX - getElementPosition(element).x)
  centerY = Math.abs(event.touches[0].pageY - getElementPosition(element).y)
  context.clearRect(0, 0, element.width, element.height)
  draw()
}

var pressOver = function () {
  // 如果这个元素的位置内只有一个手指的话
  if (event.targetTouches.length === 1) {
    // console.log('当前只有一个触点')
  }
  // event.toElement.parentElement.dataset.color
  color = 'rgba(0, 0, 0, 0)'
  element = event.srcElement
  context = element.getContext('2d')
  context.clearRect(0, 0, element.width, element.height)
  context.fillStyle = color
  context.fill()
}

var init = function () {
  containers = document.getElementsByClassName('material-design')
  containers = Array.prototype.slice.call(containers)

  for (var i = 0; i < containers.length; i += 1) {
    canvas = document.createElement('canvas')
    canvas.addEventListener('touchstart', press, false)
    canvas.addEventListener('touchend', pressOver, false)
    canvas.style.borderRadius = '10px'
    containers[i].appendChild(canvas)
    canvas.style.width = '100%'
    canvas.style.height = '100%'
    canvas.width = canvas.offsetWidth
    canvas.height = canvas.offsetHeight
  }
}

export default function setWater () {
  init()
}
