<template>
  <div class="record-circle">
    <div id="listBody" style="overflow-y: auto;padding-bottom: 120px;">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有了更多了，欢迎继续打卡噢！"
        @load="onLoad">
        <div :class="'circle-single-issue ' + (item.recordMinute > 300 ? 'basic center' : '')"
             v-for="item in records"
             :ref="'record' + item.id"
             :key="item.recordId">
          <div class="oct-flex-row-v-center">
            <div>
              <div :class="'circle-issue-date ' + (item.recordMinute > 300 ? 'shadow' : '')">
                {{getTimeStr(item.recordDate, 'YYYY年MM月DD日')}}
              </div>
              <div class="record-small-date">
                <el-tag effect="dark" size="mini" type="success" style="margin: 5px 10px 5px 0"> {{'总时长：' + item.recordMinute + '分钟'}}</el-tag>
                <el-tag effect="dark" size="mini" type="warning" style="margin: 5px 10px 5px 0"> {{'打卡次数：' + item.recordNums}}</el-tag>
              </div>
            </div>
          </div>
          <div v-show="showDetails[item.id]">
            <van-loading v-show="currRecords[item.id] === undefined" type="spinner" color="#1989fa"></van-loading>
            <div v-for="curr in currRecords[item.id]" :key="'currRecord' + curr.id">
              <el-tag effect="plain" type="info" size="medium" style="margin: 5px 0 2px 0">
                <b>{{getTagContent(curr)[0].name}}</b>
                <span>{{getTagContent(curr)[0].value}}</span>
                <span style="width: 10px;">，</span>
                <b>{{getTagContent(curr)[1].name}}</b>
                <span>{{getTagContent(curr)[1].value}}</span>
              </el-tag>
            </div>
          </div>
          <div :class="getContent(item).class"></div>
          <div class="plan-solve-content">{{getContent(item).content}}</div>
          <div class="tools-bottom-right oct-flex-col-all-center" :style="'background-color: ' + getContent(item).color" @click="showRecords(item)">
            <el-icon class="el-icon-arrow-down" v-show="!showDetails[item.id]"></el-icon>
            <el-icon class="el-icon-arrow-up" v-show="showDetails[item.id]"></el-icon>
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import clickWaterJs from '../../utils/clickWaterJs.js'
import { getRecordDayByPage, getRecordByDay } from '@/api/study'

export default {
  name: 'RecordCircle',
  data () {
    return {
      data: [{
        year: '1951 年',
        sales: 38
      },
      {
        year: '1952 年',
        sales: 52
      },
      {
        year: '1956 年',
        sales: 61
      },
      {
        year: '1957 年',
        sales: 145
      },
      {
        year: '1958 年',
        sales: 48
      },
      {
        year: '1959 年',
        sales: 38
      },
      {
        year: '1960 年',
        sales: 38
      },
      {
        year: '1962 年',
        sales: 38
      }
      ],
      showDetails: [],
      loading: false,
      finished: false,
      date: new Date(),
      page: {
        startPage: 1,
        pageNum: 5
      },
      total: 0,
      records: [],
      currRecords: [],
      currCharts: [],
      tooltip: {
        showItemMarker: false,
        onShow: function (ev) {
          var items = ev.items
          items[0].name = null
          items[0].name = items[0].title
          items[0].value = items[0].value + '分钟'
        }
      }
    }
  },
  mounted () {
    this.records = []
    this.page.startPage = 1
    this.initRecord()
  },
  methods: {
    getTagContent (item) {
      return [{
        name: '打卡区间：',
        value: `${this.$moment(new Date(item.startTime)).format('HH:mm')} - ${this.$moment(new Date(item.endTime)).format('HH:mm')}`
      },
      {
        name: '打卡时长：',
        value: this.getDiffTime(new Date(item.startTime), new Date(item.endTime))
      }]
    },
    async showRecords (item) {
      if (this.showDetails[item.id] === undefined || this.showDetails[item.id] === false) {
        this.showDetails[item.id] = true
      } else {
        this.showDetails[item.id] = false
      }
      this.$forceUpdate()
      const data = {
        recordId: item.recordId,
        recordDate: item.recordDate
      }
      await getRecordByDay(data)
        .then((res) => {
          if (res.data.data !== null) {
            this.currRecords[item.id] = res.data.data
            let result = []
            res.data.data.forEach((item) => {
              result.push({
                time: `${this.$moment(new Date(item.startTime)).format('HH:mm')} - ${this.$moment(new Date(item.endTime)).format('HH:mm')}`,
                cost: this.$moment(new Date(item.endTime)).diff(this.$moment(new Date(item.startTime)), 'minutes')
              })
            })
            this.currCharts[item.id] = result
            this.$forceUpdate()
          }
        })
    },
    getContent (item) {
      let res = {
        content: '',
        class: '',
        background: ''
      }
      let time = item.recordMinute
      if (time >= 0 && time <= 60) {
        res.content = '< 1'
        res.class = 'plan-solve-one'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), rgba(248, 246, 242, 1) 40%, rgba(0, 0, 0, 0.3))'
        res.color = 'rgba(0, 0, 0, 0.8)'
      } else if (time > 60 && time <= 180) {
        res.content = '< 3'
        res.class = 'plan-solve-three'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), #ecf9e1 40%, rgba(147, 210, 170, 0.6))'
        res.color = 'rgba(147, 210, 170, 0.9)'
      } else if (time > 180 && time <= 300) {
        res.content = '< 5'
        res.class = 'plan-solve-two'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), #fefdff 30%, rgba(1, 155, 219, 0.4));'
        res.color = 'rgba(1, 155, 219, 0.8)'
      } else {
        res.content = '> 5'
        res.class = 'plan-solve-four shadow'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), #fefdff 30%, rgba(138, 12, 0, 0.6));'
        res.color = 'rgba(138, 12, 0, 0.8)'
      }
      return res
    },
    onLoad () {
      this.initRecord()
    },
    async initRecord () {
      await getRecordDayByPage(this.page)
        .then((res) => {
          if (res.data.data.length > 0) {
            this.total = res.data.data[0].total
            let result = res.data.data
            result.forEach((item) => {
              if (this.records.find((plan) => plan.recordId === item.recordId) === undefined) {
                this.records.push(item)
              }
            })
          }
          // 加载状态结束
          this.loading = false

          // 数据全部加载完成
          if (this.records.length >= this.total) {
            this.finished = true
            clickWaterJs()
          } else {
            this.page.startPage += 1
          }
        })
    },
    getTimeStr (item, format) {
      item = new Date(item)
      return this.$moment(item).format(format)
    },
    getWeekDay (item) {
      item = new Date(item)
      switch (item.getDay()) {
        case 1: return '星期一'
        case 2: return '星期二'
        case 3: return '星期三'
        case 4: return '星期四'
        case 5: return '星期五'
        case 6: return '星期六'
        case 0: return '星期日'
        default: return '星期'
      }
    },
    getDiffminute (start, end) {
      const endMo = this.$moment(end)
      const startMo = this.$moment(start)
      return endMo.diff(startMo, 'minutes')
    },
    getDiffTime (start, end) {
      const endMo = this.$moment(end)
      const startMo = this.$moment(start)
      const second = endMo.diff(startMo, 'seconds')
      if (second > 60) {
        const minutes = endMo.diff(startMo, 'minutes')
        if (minutes > 60) {
          const hours = endMo.diff(startMo, 'hours')
          return hours + '时' + minutes % 60 + '分'
        } else {
          return minutes + '分' + second % 60 + '秒'
        }
      } else {
        return second + '秒'
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .plan-solve-one {
    position: absolute;
    right: 0;
    top: 0;
    border: 30px solid rgba(144, 147, 153, 0.7);
    border-left: 30px solid transparent;
    border-bottom: 30px solid transparent;
    border-top-right-radius: 10px;
  }
  .plan-solve-two {
    position: absolute;
    right: 0;
    top: 0;
    border: 30px solid rgba(1, 155, 219, 0.4);
    border-left: 30px solid transparent;
    border-bottom: 30px solid transparent;
    border-top-right-radius: 10px;
  }
  .plan-solve-three {
    position: absolute;
    right: 0;
    top: 0;
    border: 30px solid rgba(123, 200, 154, 0.8);
    border-left: 30px solid transparent;
    border-bottom: 30px solid transparent;
    border-top-right-radius: 10px;
  }
  .plan-solve-four {
    position: absolute;
    right: 0;
    top: 0;
    border: 30px solid rgba(138, 12, 0, 0.7);
    border-left: 30px solid transparent;
    border-bottom: 30px solid transparent;
    border-top-right-radius: 10px;
  }
  .plan-solve-content {
    position: absolute;
    right: 8px;
    top: 7px;
    color: whitesmoke;
  }
  .tools-bottom-right {
    position: absolute;
    bottom: 5px;
    right: 5px;
    width: 10vw;
    height: 6vw;
    color: white;
    border-radius: 20px;
  }
  .shadow {
    /* 背景颜色线性渐变 */
    /* 老式写法 */
    /* linear为线性渐变，也可以用下面的那种写法。left top，right top指的是渐变方向，左上到右上 */
    /* color-stop函数，第一个表示渐变的位置，0为起点，0.5为中点，1为结束点；第二个表示该点的颜色。所以本次渐变为两边灰色，中间渐白色 */
    background: -webkit-gradient(linear, left top, right top, color-stop(0, red), color-stop(0.2, orange), color-stop(0.3, yellow), color-stop(0.6, green), color-stop(0.8, blue), color-stop(0.9, darkBlue), color-stop(1, purple));
    /* 新式写法 */
    /* background: -webkit-linear-gradient(left top, right top, color-stop(0, #4d4d4d), color-stop(.4, #4d4d4d), color-stop(.5, white), color-stop(.6, #4d4d4d), color-stop(1, #4d4d4d)); */

    /* 设置为text，意思是把文本内容之外的背景给裁剪掉 */
    -webkit-background-clip: text;
    /* 设置对象中的文字填充颜色 这里设置为透明 */
    -webkit-text-fill-color: transparent;
    /* 每隔2秒调用下面的CSS3动画 infinite属性为循环执行animate */
    -webkit-animation: animate 2s infinite;
  }
  /* 兼容写法，要放在@keyframes前面 */
  @-webkit-keyframes animate {
    /* 背景从-100px的水平位置，移动到+100px的水平位置。如果要移动Y轴的，设置第二个数值 */
    from {background-position: -100px;}
    to {background-position: 100px;}
  }
  @keyframes animate {
    from {background-position: -100px;}
    to {background-position: 100px;}
  }
  .record-circle {
    height: 100%;

    .touch-item {
      padding: 10px;
      font-size: 16px;
      margin: 5px 0;
    }
    .touch-item:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }

    .touch-config {
      position: absolute;
      width: 30vw;
      height: auto;
      z-index: 2000;
      background-color: $bg-color;
      border-radius: 10px;
      border-top-left-radius: 0;
      box-shadow: 0 2px 4px rgba(0, 0, 0, .2), 0 0 6px rgba(0, 0, 0, .2)
    }
    .circle-single-issue {
      position: relative;
      margin: 10px 10px;
      padding: 15px 20px;
      border-radius: 10px;
      background-color: rgb(248, 246, 242);
    }
    .record-right-info {
      color: rgb(125, 105, 78);
    }
    .record-right-info span {
      font-weight: bold;
    }
    .circle-issue-date {
      color: rgb(85, 85, 85);
      font-size: $max-font;
      font-weight: bold;
    }
    .record-small-date {
      color: rgba(125, 105, 78, 0.8);
    }
    .circle-issue-icon {
      font-size: 25px;
      color: white;
      border-radius: 50%;
      width: 35px;
      height: 35px;
      margin-right: 10px;

      .circle-issue-btn {
        margin: 0 10px;
      }
    }
    .record-select {
      position: absolute;
      top: 37vh;
      right: 20px;
      font-size: 16px;
      color: rgb(165, 145, 108);
      border: 1px solid rgba(165, 145, 108, 0.3);
      background-color: rgba(165, 145, 108, 0.1);
      width: 65px;
      height: 30px;
      border-radius: 10px;
      box-shadow: 0 2px 4px rgba(165, 145, 108, 0.22), 0 0 6px rgba(0, 0, 0, .12)
    }

  }
  .record-time {
    font-size: 40px;
    font-weight: bold;
    color: rgb(125, 105, 78);
  }
  .record-start {
    font-size: 30px;
    margin-top: 10px;
    color: rgb(125, 105, 78);
    font-weight: bold;
  }
  .record-start-add {
    position: absolute;
    font-size: 50px;
    bottom: 0;
    right: -13vw;
    color: rgba(125, 105, 78, 0.8);
    font-weight: bold;
  }
  .header-logo {
    color: rgba(138, 12, 0, 1);
    position: absolute;
    left: 23px;
    top: -5px;
    font-weight: bold;
  }
</style>

<style scoped lang="scss">
  @import '../../assets/style/divBorder.css';
</style>
