<template>
  <section class="countdown">
    <div class="timer">
      <p class="text">考研倒计时</p>
      <div class="box">
        <div class="spacer"></div>
        <p class="value">{{ days }}</p>
        <p class="label">天</p>
      </div>
      <div class="box">
        <div class="spacer"></div>
        <p class="value">{{ hours }}</p>
        <p class="label">小时</p>
      </div>
      <div class="box">
        <div class="spacer"></div>
        <p class="value">{{ minutes }}</p>
        <p class="label">分</p>
      </div>
      <div class="box">
        <div class="spacer"></div>
        <p class="value">{{ seconds }}</p>
        <p class="label">秒</p>
      </div>
    </div>

    <div class="expired-timer timer">
      <div class="box">
        <div class="spacer"></div>
        <p class="value">Good Good Study</p>
        <p class="label">I'll have you in my arms</p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'OctLastCalender',
  data () {
    return {
      days: 'HI',
      hours: 'TH',
      minutes: 'ER',
      seconds: 'E!',
      timer: null,
      date: new Date()
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    show (newVal, oldVal) {
      if (!newVal) {
        clearInterval(this.timer)
        this.timer = null
      }
      if (!oldVal && newVal) {
        this.theTime()
      }
    }
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer) // 在Vue实例销毁前，清除我们的定时器
    }
    this.timer = null
  },
  mounted () {
    if (this.show) {
      this.theTime()
    }
  },
  methods: {
    theTime () {
      let that = this
      // Countdown loop
      this.timer = setInterval(function () {
        // Difference between the 2 dates
        let end = that.$moment(new Date('2020/12/21 08:30:00'))
        let now = that.$moment(this.date)
        // Time conversion to days, hours, minutes and seconds
        that.days = end.diff(now, 'days')
        that.hours = end.diff(now, 'hours') % 24
        that.minutes = end.diff(now, 'minutes') % 60
        that.seconds = end.diff(now, 'seconds') % 60
      }, 1000)
    }
  }
}
</script>

<style scoped>
  *, *::before, *::after {
    box-sizing: border-box;
  }

  .countdown {
    text-align: center;
  }

  .timer {
    -webkit-perspective: 1000px;
    perspective: 1000px;
  }
  .timer .box {
    display: inline-block;
    width: 100%;
    text-align: center;
    background: #E5989B;
    -moz-transform-style: preserve-3d;
    margin: 10px;
  }
  .timer .box:nth-child(2) .value {
    -webkit-animation-delay: 1s;
    animation-delay: 1s;
  }
  .timer .box:nth-child(3) .value {
    -webkit-animation-delay: 1.8s;
    animation-delay: 1.8s;
  }
  .timer .box:nth-child(4) .value {
    -webkit-animation-delay: 1.3s;
    animation-delay: 1.3s;
  }
  .timer .box:nth-child(5) .value {
    -webkit-animation-delay: 2s;
    animation-delay: 2s;
  }
  .timer .box .spacer {
    position: relative;
    content: '';
    display: block;
    height: 20px;
    background: #FFB4A2;
  }
  .timer .box .spacer::before, .timer .box .spacer::after {
    content: '';
    position: absolute;
    top: 5px;
    width: 10px;
    height: 10px;
    background: #FFCDB2;
    border-radius: 50%;
  }
  .timer .box .spacer::before {
    left: .8rem;
  }
  .timer .box .spacer::after {
    right: .8rem;
  }
  .timer .box .value {
    position: relative;
    margin: 0;
    font-family: "Graduate", sans-serif;
    font-size: 22px;
    color: rgba(109, 104, 117, 0.6);
    background: #FFB4A2;
    -webkit-transform-origin: top center;
    transform-origin: top center;
    -webkit-animation: wind 4s ease-out alternate infinite;
    animation: wind 4s ease-out alternate infinite;
    box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
  }
  .timer .box .label {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
    background: #E5989B;
  }
  .timer .box .label::first-letter {
    text-transform: uppercase;
  }
  .timer .text {
    margin-top: 20px;
    font-weight: 300;
    font-size: 1.25em;
    text-transform: uppercase;
    letter-spacing: 4px;
    text-align: center;
    color: #B5838D;
  }

  .expired-timer .box {
    border-radius: 10px;
    width: 100%;
  }

  @-webkit-keyframes wind {
    0% {
      -webkit-transform: rotatex(0);
      transform: rotatex(0);
      box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
      color: rgba(109, 104, 117, 0.6);
      background: #FFB4A2;
    }
    10% {
      -webkit-transform: rotatex(20deg);
      transform: rotatex(20deg);
      box-shadow: 0 15px 10px -10px #ab737e;
      color: rgba(109, 104, 117, 0.45);
      background: #ffc0b1;
    }
  }

  @keyframes wind {
    0% {
      -webkit-transform: rotatex(0);
      transform: rotatex(0);
      box-shadow: 0 15px 10px -10px rgba(181, 131, 141, 0);
      color: rgba(109, 104, 117, 0.6);
      background: #FFB4A2;
    }
    10% {
      -webkit-transform: rotatex(20deg);
      transform: rotatex(20deg);
      box-shadow: 0 15px 10px -10px #ab737e;
      color: rgba(109, 104, 117, 0.45);
      background: #ffc0b1;
    }
  }
  @-webkit-keyframes lighting {
    0% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    4% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    8% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    12% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    20% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    22% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
  }
  @keyframes lighting {
    0% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    4% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    8% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
    12% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    20% {
      box-shadow: -16px 0 0 2px white, 14px 0 0 #E5989B;
    }
    22% {
      box-shadow: -16px 0 0 2px rgba(255, 255, 255, 0.5), 14px 0 0 #E5989B;
    }
  }
  @-webkit-keyframes lighting2 {
    0% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    4% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    8% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    12% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    20% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    22% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
  }
  @keyframes lighting2 {
    0% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    4% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    8% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
    12% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    20% {
      box-shadow: 16px 0 0 2px white, -14px 0 0 #E5989B;
    }
    22% {
      box-shadow: 16px 0 0 2px rgba(255, 255, 255, 0.5), -14px 0 0 #E5989B;
    }
  }
  @media screen and (max-width: 800px) {
    .countdown {
      width: 100%;
    }
  }
  @media screen and (max-width: 480px) {
    .timer .box {
      width: 40%;
    }

    .expired-timer .box {
      width: 90%;
    }
  }
</style>
