<template>
  <div class="record-main">
    <van-tabs v-model="active"
              sticky
              background="rgba(239, 236 ,227, 1)"
              swipeable
              color="rgb(165, 145, 108)"
              animated>
      <van-tab title="每日打卡" name="recordDaily" class="oct-van-tab">
        <oct-record-circle class="record-circle"
                           ref="recordCircle"
                           @initRecordStatus="initRecordStatus"></oct-record-circle>
      </van-tab>
      <van-tab title="历史打卡" name="recordHis" class="oct-van-tab"><oct-record-his></oct-record-his></van-tab>
      <van-tab title="学习报表" name="recordChart" class="oct-van-tab">内容 3</van-tab>
    </van-tabs>
    <div class="record-action oct-flex-row-all-center to-small" @click="recordAction(recordStatus.run)">
      <van-icon v-show="!recordStatus.run" name="play"></van-icon>
      <van-icon v-show="recordStatus.run" name="stop"></van-icon>
    </div>
    <div class="record-action-right oct-flex-row-all-center">
      <van-icon name="bookmark"  @click="showCard = true"></van-icon>
    </div>
    <div class="record-action-left oct-flex-row-all-center">
      <van-icon :name="selectMe ? 'friends' : 'manager'" @click="selectMe = !selectMe"></van-icon>
    </div>
    <oct-wel-card :showCard="showCard" @changeShowCard="changeShowCard" @click="showCard = false"></oct-wel-card>

  </div>
</template>

<script>
import OctWelCard from '@/components/base/OctWelCard'
import OctRecordCircle from '@/components/record/OctRecordCircle'
import OctRecordHis from '@/components/record/OctRecordHis'
export default {
  name: 'index',
  components: { OctRecordCircle, OctWelCard, OctRecordHis },
  data () {
    return {
      active: 'recordDaily',
      selectMe: false,
      recordStatus: {
        run: false
      },
      showCard: false
    }
  },
  methods: {
    initRecordStatus (status) {
      this.recordStatus = status
    },
    recordAction (run) {
      this.recordStatus.run = run
      this.$refs.recordCircle.recordAction(run)
    },
    changeShowCard () {
      this.showCard = false
    }
  }
}
</script>

<style lang="scss">
  .record-main {
    .van-tabs__wrap {
      padding-top: 0 !important;
    }
  }
</style>

