<template>
  <div class="record-circle" @click="clickBody">
    <div class="oct-flex-row-h-center" id="topCircle" style="height: 45vh">
      <div class="wrapper" id="circleOut">
        <div class="bg" id="circleIn"></div>
        <div class="circle oct-flex-col-all-center">
          <div class="record-time">
            {{this.$moment(date).format("HH:mm")}}
            <span style="font-size: 20px;">{{this.$moment(date).format("ss")}}</span>
          </div>
          <div class="record-start">{{recordStatus.run ? 'End !' : 'Start !'}}</div>
          <div class="record-start-add" @click="addRecord"><el-icon class="el-icon-circle-plus"></el-icon></div>
        </div>
      </div>
    </div>
    <div id="listBody" style="overflow-y: auto;overflow-y: auto;padding-bottom: 120px;">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有了更多了，欢迎继续打卡噢！"
        @load="onLoad">
        <!-- class是material-design的都会有ClickWater效果-->
        <div class="circle-single-issue material-design"
             v-for="item in records"
             :ref="'record' + item.id"
             :key="item.recordId"
             @touchstart="touchStart(item, $event)"
             @touchend="touchEnd(item, $event)"
             :style="getContent(item).background">
          <div :id="'touchConfig' + item.id" v-show="showConfig[item.id]" class="touch-config">
            <div class="touch-item" @click="changeTime">
              修改时间
            </div>
            <div class="touch-item" @click="deleteRecord">
              删除打卡
            </div>
            <div class="touch-item" @click="cancelRecord">
              取消
            </div>
          </div>
          <div class="oct-flex-row-v-center">
              <div style="border-right: 4px dotted rgba(125, 105, 78, 1);padding-right: 15px;">
                <div class="circle-issue-date">
                  {{getTimeStr(item.startTime, 'HH:mm')}} - {{item.status === 1 ? $moment(date).format('HH:mm') : getTimeStr(item.endTime, 'HH:mm')}}
                </div>
                <div class="record-small-date">{{getTimeStr(item.startTime, 'YYYY年MM月DD日 ')}}{{getWeekDay(item.startTime)}}</div>
              </div>
              <div style="margin-left: 15px;">
                <div class="oct-flex-row-v-center record-right-info" style="margin-bottom: 5px;position: relative">
                  <van-image :src="item.avatar" fit="fill" round width="25" height="25" :style="getContent(item).content === '> 5' ? 'border: 1px solid rgba(138, 12, 0, 0.8)' : 'border: 1px solid rgb(165, 145, 108)'"></van-image>
                  <div class="header-logo" v-show="getContent(item).content === '> 5'"><van-icon name="fire"></van-icon></div>
                  <div :style="getContent(item).content === '> 5' ? 'margin-left: 10px;color: rgba(138, 12, 0, 0.8);font-weight: bold;' : 'margin-left: 10px'">{{item.nickName}}</div>
                </div>
                <div  v-show="item.status === 2">
                  <div :class="getContent(item).content === '> 5' ? 'shadow record-right-info' : 'record-right-info'"><span>打卡时长: </span>{{getDiffTime(new Date(item.startTime), new Date(item.endTime))}}</div>
                </div>
                <div v-show="item.status === 1">
                  <div class="record-right-info"><span>进行时长: </span>{{getDiffTime(new Date(item.startTime), date)}}</div>
                </div>
              </div>
            </div>
            <div :class="getContent(item).class"></div>
            <div class="plan-solve-content">{{getContent(item).content}}</div>
        </div>
      </van-list>
    </div>

    <van-overlay :show="showChange" @click="showChange = false;showCalender = false">
      <div @click.stop class="change-content">
        <div class="change-title">{{addMode ? '手工新增记录' : '修改打卡记录'}}</div>
        <div class="fixed-right" @click="showChange = false;showCalender = false"><el-icon class="el-icon-error"></el-icon></div>
        <div class="change-body">
          <van-cell title="起始时间" :value="$moment(startTime).format('YYYY/MM/DD HH:mm:ss ')" is-link @click="setCalenTime(startTime, 'start')"></van-cell>
          <van-cell title="结束时间" :value="$moment(endTime).format('YYYY/MM/DD HH:mm:ss ')" is-link @click="setCalenTime(endTime, 'end')"></van-cell>
          <el-collapse-transition>
            <div v-show="showCalender">
              <van-datetime-picker v-model="selectTime"
                                   type="datetime"
                                   :show-toolbar="false"
                                   :visible-item-count="4"
                                   @change="handleChange">
              </van-datetime-picker>
            </div>
          </el-collapse-transition>
        </div>
        <el-button type="text" class="confirm-btn" @click="submitChange">确 认</el-button>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import clickWaterJs from '../../utils/clickWaterJs.js'
import { getRecordByPage, nowStatus, saveRecord, changeRecord, deleteRecord, addRecordByPeople } from '@/api/study'

export default {
  name: 'RecordCircle',
  data () {
    return {
      addMode: false,
      showCalender: false,
      showChange: false,
      showCard: false,
      selectMe: true,
      loading: false,
      finished: false,
      timer: null,
      date: new Date(),
      page: {
        startPage: 1,
        pageNum: 5
      },
      total: 0,
      records: [],
      recordStatus: {
        run: false,
        record: Object
      },
      loop: null,
      canvas: {},
      centerX: 0,
      centerY: 0,
      color: '',
      context: {},
      element: {},
      radius: 0,
      configItem: null,
      showConfig: [],
      startTime: new Date(),
      endTime: new Date(),
      selectTime: new Date(),
      selectWhich: 'start'
    }
  },
  watch: {
    configItem (newVal) {
      this.startTime = new Date(newVal.startTime)
      this.endTime = new Date(newVal.endTime)
    },
    addMode (newVal) {
      if (newVal) {
        this.startTime = new Date()
        this.endTime = new Date()
      }
    },
    showChange (newVal) {
      if (!newVal) {
        this.addMode = false
      }
    }
  },
  mounted () {
    this.records = []
    this.page.startPage = 1
    this.initRecord()
    this.initCircleStyle()
    this.initTime()
    this.initStatus()
  },
  beforeDestroy () {
    if (this.timer) {
      clearInterval(this.timer) // 在Vue实例销毁前，清除我们的定时器
    }
    this.timer = null
  },
  methods: {
    async submitChange () {
      this.showChange = false
      if (this.addMode) {
        this.addMode = false
        const data = {
          status: 2,
          startTime: this.startTime,
          endTime: this.endTime
        }
        await addRecordByPeople(data)
          .then(() => {
            this.$notify.success('添加成功')
            this.page.startPage = 1
            this.loading = false
            this.finished = false
            this.records = []
            this.initStatus()
          })
      } else {
        const data = {
          recordId: this.configItem.recordId,
          startTime: this.startTime,
          endTime: this.endTime
        }
        await changeRecord(data)
          .then(() => {
            this.page.startPage = 1
            this.loading = false
            this.finished = false
            this.records = []
            this.initStatus()
          })
      }
    },
    setCalenTime (time, which) {
      this.showCalender = true
      this.selectTime = time
      this.selectWhich = which
    },
    handleChange () {
      if (this.selectWhich === 'start') {
        this.startTime = this.selectTime
      } else {
        this.endTime = this.selectTime
      }
    },
    changeTime () {
      this.showChange = true
    },
    addRecord () {
      this.showChange = true
      this.addMode = true
    },
    cancelRecord () {
      this.showConfig = []
    },
    async deleteRecord () {
      const data = {
        recordId: this.configItem.recordId
      }
      await deleteRecord(data)
        .then(() => {
          this.$notify.success('删除成功!')
          this.page.startPage = 1
          this.loading = false
          this.finished = false
          this.records = []
          this.initStatus()
        })
    },
    clickBody () {
      // setTimeout(function () {
      //   this.showConfig[this.configItem.id] = false
      // }.bind(this), 100)
    },
    touchStart (item, event) {
      clearTimeout(this.loop) // 再次清空定时器，防止重复注册定时器
      this.loop = setTimeout(function () {
        this.showConfig[item.id] = true
        this.configItem = item
        const element = document.getElementById('touchConfig' + item.id)
        element.style.left = Math.abs(event.touches[0].pageX - this.getElementPosition(this.$refs['record' + item.id][0]).x) + 'px'
        element.style.top = Math.abs(event.touches[0].pageY - this.getElementPosition(this.$refs['record' + item.id][0]).y) + 'px'
      }.bind(this), 500)
    },
    getElementPosition (e) {
      var x = 0; var y = 0
      while (e != null) {
        x += e.offsetLeft
        y += e.offsetTop
        e = e.offsetParent
      }
      return { x: x, y: y }
    },
    touchEnd (item) {
      clearTimeout(this.loop) // 再次清空定时器，防止重复注册定时器
    },
    getContent (item) {
      let res = {
        content: '',
        class: '',
        background: ''
      }
      let time
      if (item.status === 1) {
        time = this.getDiffminute(new Date(item.startTime), this.date)
      } else {
        time = this.getDiffminute(new Date(item.startTime), new Date(item.endTime))
      }
      if (time >= 0 && time <= 60) {
        res.content = '< 1'
        res.class = 'plan-solve-one'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), rgba(248, 246, 242, 1) 40%, rgba(0, 0, 0, 0.3))'
      } else if (time > 60 && time <= 180) {
        res.content = '< 3'
        res.class = 'plan-solve-three'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), #ecf9e1 40%, rgba(147, 210, 170, 0.6))'
      } else if (time > 180 && time <= 300) {
        res.content = '< 5'
        res.class = 'plan-solve-two'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), #fefdff 30%, rgba(1, 155, 219, 0.4));'
      } else {
        res.content = '> 5'
        res.class = 'plan-solve-four shadow'
        res.background = 'background: linear-gradient(to top left, rgba(248, 246, 242, 1), #fefdff 30%, rgba(138, 12, 0, 0.6));'
      }
      return res
    },
    async initStatus () {
      await nowStatus()
        .then((res) => {
          if (res.data.data !== null) {
            const result = res.data.data
            // 30表示没有开始事件
            if (result.status === 30) {
              this.recordStatus.run = false
            }
            // 有正在开始的事件
            if (result.status === 31) {
              this.recordStatus.run = true
              this.recordStatus.record = result
            }
            // 存在多个开始的事件
            if (result.status === 100) {
              // 暂时不处理
            }
          }
          this.$emit('initRecordStatus', this.recordStatus)
        })
    },
    async initRecord () {
      await getRecordByPage(this.page)
        .then((res) => {
          if (res.data.data.length > 0) {
            this.total = res.data.data[0].total
            let result = res.data.data
            result.forEach((item) => {
              if (this.records.find((plan) => plan.recordId === item.recordId) === undefined) {
                this.records.push(item)
              }
            })
          }
          // 加载状态结束
          this.loading = false

          // 数据全部加载完成
          if (this.records.length >= this.total) {
            this.finished = true
            clickWaterJs()
          } else {
            this.page.startPage += 1
          }
        })
    },
    async recordAction (run) {
      this.recordStatus.run = run
      const data = {
        status: run ? 2 : 1
      }
      if (run) {
        data.endTime = this.date
        data.recordId = this.recordStatus.record.recordId
      } else {
        data.startTime = this.date
      }
      await saveRecord(data)
        .then(() => {
          this.page.startPage = 1
          this.loading = false
          this.finished = false
          this.records = []
          this.initStatus()
          // this.initRecord()
        })
    },
    changeShowCard () {
      this.showCard = false
    },
    onLoad () {
      this.initRecord()
    },
    initTime () {
      let _this = this // 声明一个变量指向Vue实例this，保证作用域一致
      this.timer = setInterval(() => {
        _this.date = new Date() // 修改数据date
      }, 1000)
    },
    initCircleStyle () {
      let width = document.body.clientWidth
      let height = document.body.clientHeight
      document.getElementById('circleOut').style.width = width * 0.6 + 'px'
      document.getElementById('circleOut').style.height = width * 0.6 + 'px'
      document.getElementById('circleIn').style.width = width * 0.6 + 'px'
      document.getElementById('circleIn').style.height = width * 0.6 + 'px'
      document.getElementById('topCircle').style.height = (width * 0.6 + 50) + 'px'
      document.getElementById('listBody').style.height = (height - document.getElementById('topCircle').style.height) + 'px'
    },
    getTimeStr (item, format) {
      item = new Date(item)
      return this.$moment(item).format(format)
    },
    getWeekDay (item) {
      item = new Date(item)
      switch (item.getDay()) {
        case 1: return '星期一'
        case 2: return '星期二'
        case 3: return '星期三'
        case 4: return '星期四'
        case 5: return '星期五'
        case 6: return '星期六'
        case 0: return '星期日'
        default: return '星期'
      }
    },
    getDiffminute (start, end) {
      const endMo = this.$moment(end)
      const startMo = this.$moment(start)
      return endMo.diff(startMo, 'minutes')
    },
    getDiffTime (start, end) {
      const endMo = this.$moment(end)
      const startMo = this.$moment(start)
      const second = endMo.diff(startMo, 'seconds')
      if (second > 60) {
        const minutes = endMo.diff(startMo, 'minutes')
        if (minutes > 60) {
          const hours = endMo.diff(startMo, 'hours')
          return hours + '时' + minutes % 60 + '分'
        } else {
          return minutes + '分' + second % 60 + '秒'
        }
      } else {
        return second + '秒'
      }
    }
  }
}
</script>

<style scoped>
  @import '../../assets/style/clickWater.css';
  @import '../../assets/style/divBorder.css';

</style>

<style scoped lang="scss">
  .shadow {
    text-align: center;
    /* 背景颜色线性渐变 */
    /* 老式写法 */
    /* linear为线性渐变，也可以用下面的那种写法。left top，right top指的是渐变方向，左上到右上 */
    /* color-stop函数，第一个表示渐变的位置，0为起点，0.5为中点，1为结束点；第二个表示该点的颜色。所以本次渐变为两边灰色，中间渐白色 */
    background: -webkit-gradient(linear, left top, right top, color-stop(0, #4d4d4d), color-stop(.4, rgba(138, 12, 0, 0.7)), color-stop(.5, rgba(138, 12, 0, 0.7)), color-stop(.6, #4d4d4d), color-stop(1, #4d4d4d));
    /* 新式写法 */
    /* background: -webkit-linear-gradient(left top, right top, color-stop(0, #4d4d4d), color-stop(.4, #4d4d4d), color-stop(.5, white), color-stop(.6, #4d4d4d), color-stop(1, #4d4d4d)); */

    /* 设置为text，意思是把文本内容之外的背景给裁剪掉 */
    -webkit-background-clip: text;
    /* 设置对象中的文字填充颜色 这里设置为透明 */
    -webkit-text-fill-color: transparent;
    /* 每隔2秒调用下面的CSS3动画 infinite属性为循环执行animate */
    -webkit-animation: animate 2s infinite;
  }
  /* 兼容写法，要放在@keyframes前面 */
  @-webkit-keyframes animate {
    /* 背景从-100px的水平位置，移动到+100px的水平位置。如果要移动Y轴的，设置第二个数值 */
    from {background-position: -100px;}
    to {background-position: 100px;}
  }
  @keyframes animate {
    from {background-position: -100px;}
    to {background-position: 100px;}
  }
  .record-circle {
    height: 100%;

    .change-content {
      height: 50vh;
      width: 100vw;
      background-color: white;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
      position: fixed;
      bottom: 0;

      .confirm-btn{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 6vh;
        background-color: rgba(165, 145, 108, 0.8);
        color: white;
        font-size: 19px;
      }

      .change-title {
        position: absolute;
        left: 15px;
        top: 2vh;
        font-size: 18px;
      }

      .fixed-right {
        position: absolute;
        top: 2vh;
        right: 20px;
        width: 100%;
        display: flex;
        color: rgba(165, 145, 108, 1);
        font-size: 25px;
        flex-direction: row-reverse;
      }

      .change-body {
        position: absolute;
        top: 7vh;
        width: 100%;
        font-size: 18px;
      }
    }

    .touch-item {
      padding: 10px;
      font-size: 16px;
      margin: 5px 0;
    }
    .touch-item:hover {
      background-color: rgba(0, 0, 0, 0.1);
    }

    .touch-config {
      position: absolute;
      width: 30vw;
      height: auto;
      z-index: 2000;
      background-color: $bg-color;
      border-radius: 10px;
      border-top-left-radius: 0;
      box-shadow: 0 2px 4px rgba(0, 0, 0, .2), 0 0 6px rgba(0, 0, 0, .2)
    }

    .circle-single-issue {
      position: relative;
      margin: 10px 10px;
      padding: 15px 20px;
      border-radius: 10px;
    }

    .record-right-info {
      color: rgb(125, 105, 78);
    }
    .record-right-info span {
      font-weight: bold;
    }
    .circle-issue-date {
      color: rgb(125, 105, 78);
      font-size: $great-font;
      font-weight: bold;
    }
    .record-small-date {
      color: rgba(125, 105, 78, 0.8);
    }
    .circle-issue-icon {
      font-size: 25px;
      color: white;
      border-radius: 50%;
      width: 35px;
      height: 35px;
      margin-right: 10px;

      .circle-issue-btn {
        margin: 0 10px;
      }
    }
    .record-select {
      position: absolute;
      top: 37vh;
      right: 20px;
      font-size: 16px;
      color: rgb(165, 145, 108);
      border: 1px solid rgba(165, 145, 108, 0.3);
      background-color: rgba(165, 145, 108, 0.1);
      width: 65px;
      height: 30px;
      border-radius: 10px;
      box-shadow: 0 2px 4px rgba(165, 145, 108, 0.22), 0 0 6px rgba(0, 0, 0, .12)
    }

  }
  .record-time {
    font-size: 40px;
    font-weight: bold;
    color: rgb(125, 105, 78);
  }
  .record-start {
    font-size: 30px;
    margin-top: 10px;
    color: rgb(125, 105, 78);
    font-weight: bold;
  }
  .record-start-add {
    position: absolute;
    font-size: 50px;
    bottom: 0;
    right: -13vw;
    color: rgba(125, 105, 78, 0.8);
    font-weight: bold;
  }
  .header-logo {
    color: rgba(138, 12, 0, 1);
    position: absolute;
    left: 23px;
    top: -5px;
    font-weight: bold;
  }
</style>

<!-- 圆圈CSS -->
<style scoped>
  .wrapper {
    margin-bottom: 30px;
    margin-top: 2.5vh;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: rgb(165, 145, 108);
    position: relative;
  }
  .bg {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: absolute;
    top: 0;
    left: 0;
    animation: pulse 3s infinite linear;
  }
  .bg-run {
    position: absolute;
    top: -15px;
    left: 120px;
    width: 10px;
    height: 10px;
    background-color: white;
    border: 1px solid white;
    animation: rond 60s linear infinite;
    transform-origin: 50% 140px;
    border-radius: 50%;
  }
  .plan-solve-one {
    position: absolute;
    right: 0;
    bottom: 0;
    border: 30px solid rgba(144, 147, 153, 0.7);
    border-left: 30px solid transparent;
    border-top: 30px solid transparent;
    border-bottom-right-radius: 10px;
  }
  .plan-solve-two {
    position: absolute;
    right: 0;
    bottom: 0;
    border: 30px solid rgba(1, 155, 219, 0.4);
    border-left: 30px solid transparent;
    border-top: 30px solid transparent;
    border-bottom-right-radius: 10px;
  }
  .plan-solve-three {
    position: absolute;
    right: 0;
    bottom: 0;
    border: 30px solid rgba(123, 200, 154, 0.8);
    border-left: 30px solid transparent;
    border-top: 30px solid transparent;
    border-bottom-right-radius: 10px;
  }
  .plan-solve-four {
    position: absolute;
    right: 0;
    bottom: 0;
    border: 30px solid rgba(138, 12, 0, 0.7);
    border-left: 30px solid transparent;
    border-top: 30px solid transparent;
    border-bottom-right-radius: 10px;
  }
  .plan-solve-content {
    position: absolute;
    right: 8px;
    bottom: 5px;
    color: whitesmoke;
  }

  @keyframes rond {
    0% {
      transform : rotate(0deg);
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.3);
    }
    50% {
      transform : rotate(180deg);
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.5);
    }
    100% {
      transform : rotate(360deg);
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.3);
    }
  }
  .circle {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: rgb(248, 246, 242);
  }
  @keyframes pulse {
    0% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.3);
    }
    10% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.4);
    }
    20% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.5);
    }
    30% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.6);
    }
    40% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.7);
    }
    50% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.8);
    }
    60% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 0.9);
    }
    70% {
      box-shadow: 0 0 10px 10px rgba(165, 145, 108, 1);
    }
