<template>
  <van-overlay :show="show" @click="clickOverLay">
    <div class="wrapper">
      <div class="block">
        <oct-last-calender :show="show" style="height: 65%;border-bottom: 3px dotted rgb(255, 191, 176)"></oct-last-calender>
        <div class="oct-flex-col-all-center;height: 100%;">
          <iframe scrolling="no"
                  width="100%"
                  src="https://tianqiapi.com/api.php?style=ya&skin=durian&cityid=101210106"
                  frameborder="0"
                  allowtransparency="true"></iframe>
        </div>
      </div>
    </div>
  </van-overlay>
</template>
<script>
import OctLastCalender from '@/components/base/OctLastCalender'

export default {
  name: 'AppHome',
  components: { OctLastCalender },
  props: {
    showCard: {
      type: Boolean,
      default: false
    }
  },
  mounted () {
    this.show = this.showCard
  },
  watch: {
    showCard (newVal) {
      this.show = newVal
    }
  },
  data () {
    return {
      show: true,
      timer: null
    }
  },
  beforeDestroy () {
    this.show = false
    this.$emit('changeShowCard')
  },
  methods: {
    clickOverLay () {
      this.show = false
      this.$emit('changeShowCard')
    }
  }

}
</script>

<style scoped>
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }
  .block {
    width: 70%;
    min-width: 60vw;
    border-radius: 10px;
    border: 2px solid rgba(255, 186, 167, 0.8);
    background-color: rgba(255, 233, 223, 0.8);
  }
</style>
